function NotFoundPage() {
  let warning = "";

  let rand = Math.random();
  if (rand <= 0.3) {
    warning = " !";
  } else if (rand <= 0.6) {
    warning = "  .";
  } else if (rand <= 0.9) {
    warning = "  .";
  } else {
    warning = " !";
  }

  return (
    <div className="not-found-page">
      {warning}
      <br />
      a.k.a.
      <br />
      404 Page Not Found
    </div>
  );
}

export default NotFoundPage;
